<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process197"></a>

<p><br></p>
<p><span class="rvts17">来自异乡的异人（</span><span class="rvts50">Stranger in a Strange Land</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:621,Y:173</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：威尔克斯</span><span class="rvts20"></span><span class="rvts21">科瑞瓦的灵魂（</span><span class="rvts20">The Spirit of Vilnx Creeva</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助威尔克斯的灵魂脱离苦海</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1235.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在石墓花园一个紧挨着大门的地方（坐标</span><span class="rvts20">X:621,Y:173</span><span class="rvts21">），可以发现一处隐藏的土堆（需要一定的智慧</span><span class="rvts20">Wit</span><span class="rvts21">值），挖开它，得到一支蜥蜴腿。释放</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">技能，可以看到一只蜥蜴的灵魂，他会要你帮助它把它的这支遗骸带到有两只石头火龙喷火的地方，进行焚烧。</span>
<br><span class="rvts21">通过对话我们不难发现，他其实就是喷火龙处宝箱的主人。玩家可以选择吃掉蜥蜴腿，获得打开宝箱的密码（详见</span><span class="rvts20"></span><span class="rvts21">用交叉舌交谈</span><span class="rvts20"></span><span class="rvts21">支线），但是这样会受到该灵魂的诅咒，并且该任务将不会关闭。</span>
<br><span class="rvts21">最好的办法依然是把它的腿带到喷火龙处进行焚烧，使该灵魂得到平息。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



